/* src/components/Header.css */
.header {
  background-color: #333;
  color: white;
  padding: 10px 0;
}

.header h1 {
  text-align: center;
  margin: 0;
}

.header nav ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  padding: 0;
}

.header nav ul li {
  margin: 0 15px;
}

.header nav ul li a {
  color: white;
  text-decoration: none;
}

.header nav ul li a:hover {
  text-decoration: underline;
}
