/* src/components/Footer.css */

.footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 20px;
  position: absolute;
  bottom: 0;
  width: 100%;
}

.footer p {
  margin: 0;
  font-size: 14px;
}

.footer a {
  color: #4caf50; /* Gaya warna hijau untuk tautan */
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}
