/* src/components/Projects.css */

.projects {
  background-color: #f8f9fa;
  padding: 40px 20px;
  margin: 20px auto;
  max-width: 1000px;
}

.projects h2 {
  text-align: center;
  font-size: 36px;
  color: #333;
  margin-bottom: 30px;
}

.project-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.project-item {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: calc(33% - 20px); /* Adjust the width for responsiveness */
  padding: 20px;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
}

.project-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.project-item img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 15px;
}

.project-item h3 {
  font-size: 24px;
  color: #333;
  margin: 10px 0;
}

.project-item p {
  font-size: 16px;
  color: #555;
}

.project-item a {
  display: inline-block;
  margin-top: 10px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.project-item a:hover {
  background-color: #0056b3;
}
