/* src/components/About.css */

.about {
  background-color: #f0f0f0;
  padding: 40px 20px;
  margin: 20px auto;
  max-width: 800px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.about h2 {
  text-align: center;
  font-size: 32px;
  color: #333;
  margin-bottom: 20px;
}

.about p {
  font-size: 18px;
  line-height: 1.6;
  color: #555;
  text-align: justify;
}
